@import url(https://fonts.googleapis.com/css?family=Codystar|Montserrat:400,500&display=swap);
 *{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size: 10px;
}
body{
  background: #1b264f;
}
.heading{
  font-family: Codystar,sans-serif;
  color: #f4de32;
  background-image: -webkit-gradient(linear,left top, right top,from(#4b16ce),to(#ff393f));
  background-image: -webkit-linear-gradient(left,#4b16ce,#ff393f);
  background-image: linear-gradient(to right,#4b16ce,#ff393f);
  font-size: 4.5rem;
  font-weight: bolder;
  padding: 2rem;
  -webkit-clip-path: polygon(0 0, 100% 0%, 100% 50%, 0% 100%);
          clip-path: polygon(0 0, 100% 0%, 100% 50%, 0% 100%);

}
.category-container{
  width:70%;
  min-height: 100vh;
  margin:0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  align-content: center;
}
.category-name{
  width: 18rem;
  height: 15rem;
  background: #fff;
  font-family: Montserrat;
  text-align: center;
  margin:1rem;
  border-radius: 1rem;
  box-shadow: 0 0.625rem 1.25rem rgba(0,0,0,0.1), 0 0.375rem 0.375rem rgba(0,0,0,0.05);
  cursor: pointer;
  color:#444;
  -webkit-transition: 0.1s all ease-out;
  transition: 0.1s all ease-out;
}
.category-name:hover{
  margin-top: -0.5rem;
  color:#19b7d2;
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
  font-weight: bold;
}
.category-text{
  padding-top: 2rem;
  font-size: 2rem;
  font-family: sans-serif;
}
.category-logo{
  margin-top: 2rem;
  width:100%;
  height: 30%;
}

.quiz-container{
  width: 60%;
  margin: 0 auto;
  font-family: Montserrat;
}
.loader {
  content: '';
    position: absolute;
    width: 6.25rem;
    height: 6.25rem;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1000;
    margin: auto;
    -webkit-animation: loader-rotation .8s infinite linear;
    animation: loader-rotation .8s infinite linear;
    border-left: 3px solid rgba(255, 255, 255, 0.15);
    border-right: 3px solid rgba(255, 255, 255, 0.15);
    border-bottom: 3px solid rgba(255, 255, 255, 0.15);
    border-top: 3px solid rgba(208, 32, 1, 0.8);
    border-radius: 100%;
}

@-webkit-keyframes loader-rotation {
  0% {
    -webkit-transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg); } }

@keyframes loader-rotation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
}
button:focus
 {outline:0;}

.quiz-info{
  width:auto;
  height: 65vh;
  position: relative;
}
  @-webkit-keyframes bounceIn{
    0%       { bottom:5px; }
    25%, 75% { bottom:15px; }
    50%      { bottom:20px; }
    100%     {bottom:0;}
}
  @keyframes bounceIn{
    0%       { bottom:5px; }
    25%, 75% { bottom:15px; }
    50%      { bottom:20px; }
    100%     {bottom:0;}
}
.back-btn,.next{
  background-color: transparent;
  background-repeat: no-repeat;
  border-top: transparent;
  border-left: transparent;
  border-right: transparent;
  border-bottom:0.34rem solid #f9dd23;
  color: #f9dd23;
  padding: 0.45rem;
  font-size: 2.5rem;
  cursor: pointer;
}
.quiz-ques{
  font-size: 3rem;
  margin-top: 5rem;
  color: #f2f0f0;
}
.option-list{
  text-align: center;
  margin-top: 2rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  align-content: center;
}
.options{
  list-style: none;
  width: 40%;
  font-size: 2rem;
  text-align: center;
  margin:1rem 2rem;
  padding: 2rem;
  cursor: pointer;
  background:#fff;
  border-radius: 0.5rem;
  color:#033d55;
  box-shadow: 0 0.625rem 1.25rem rgba(0,0,0,0.1), 0 0.375rem 0.375rem rgba(0,0,0,0.6);
  -webkit-transition: all 0.1s cubic-bezier(.25,.8,.25,1);
  transition: all 0.1s cubic-bezier(.25,.8,.25,1);
}
.options:hover{
  box-shadow: 0 0.625rem 1.25rem rgba(0,0,0,0.1), 0 0.375rem 0.375rem rgba(0,0,0,1);
  margin-top: 0.7rem;
}
.next{
  padding:0.5rem 2.5rem;
  border: 0.2rem solid #f98723;
  position: absolute;
  bottom: 3rem;
  right:3rem;
  color: #f98723;
  box-shadow: 0 0.625rem 1.25rem rgba(0,0,0,0.1), 0 0.375rem 0.375rem rgba(0,0,0,0.3);
  -webkit-transition: all 0.2s cubic-bezier(.25,.8,.25,1);
  transition: all 0.2s cubic-bezier(.25,.8,.25,1);
}
.next:hover{
  background-color: #f98723;
  color: #fff;
}
.green{
  background: #05b857;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-animation: pulse 0.4s;
          animation: pulse 0.4s;
}
.red{
  background: #ff2828;
  -webkit-animation: shake 5s;
          animation: shake 5s;
  position: relative;
}
@-webkit-keyframes pulse {
    0% {
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.5);
    }

    70% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        box-shadow: 0 0 0 10px rgba(44, 209, 15, 0.3);
    }

    100% {
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
}
@keyframes pulse {
    0% {
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.5);
    }

    70% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        box-shadow: 0 0 0 10px rgba(44, 209, 15, 0.3);
    }

    100% {
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
}

@-webkit-keyframes shake {
  0% {left: 0}
  1% {left: -3rem}
  2% {left: 5rem}
  3% {left: -8rem}
  4% {left: 8rem}
  5% {left: -5rem}
  6% {left: 3rem}
  7% {left: 0}
}

@keyframes shake {
  0% {left: 0}
  1% {left: -3rem}
  2% {left: 5rem}
  3% {left: -8rem}
  4% {left: 8rem}
  5% {left: -5rem}
  6% {left: 3rem}
  7% {left: 0}
}
.quiz-over{
  width:100%;
  margin-top: 10rem;
}
.final-score{
  color:#fff;
  font-size: 4rem;
  padding: 2rem;
  margin-bottom: 2rem;
  -webkit-animation:enlarge 0.2s;
          animation:enlarge 0.2s;
}
@-webkit-keyframes enlarge {
  0%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100%{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
}
@keyframes enlarge {
  0%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100%{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
}
.quiz-score{
  font-size: 2.5rem;
  font-weight:  bold;
  color:#e31f1f;
  position: absolute;
  top:2rem;
  right: 2.5rem;
}
.next-wrapper{
  position: relative;
  width:100%;
  height: 4rem;
  display: block;
}

@media only screen and (min-width:1300px){
  .quiz-container{
    margin-top: 5rem;
  }
  .options{
    font-size: 2.6rem;
  }
}
@media only screen and (max-width:992px){
  html{
    font-size: 10px;
  }
  .quiz-container{
    margin-top: 4rem;
  }
  .next-wrapper{
    height: 15rem;
  }
  .options{
    width:100%;
    height: 100%;
  }
  .next{
    display: block;
  }
}
@media only screen and (max-width:600px){
  html{
    font-size: 8px;
  }
  .next{
    bottom: 1rem;
  }
    .options{
      font-size: 1.6rem;
      width:100%;
    }
    .quiz-info{
      height: 100%;
    }
}

